.game-container {
  display: flex;
  background-color: green;
  width: 800px;
  height: 600px;
  background-image: url('./assets/board-data/backgammon.jpg');
  background-size: cover;
  padding-right: 10px;
}

.backgammon-board {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  padding-left: 50px;
}

.gammon-piece {
  display: flex;
  width: 50px;
  height: 100%;
  margin: -14px -3px;
  border: 3px solid transparent;
}

.gammon-piece-selected {
  border: 3px solid limegreen;
  border-radius: 50%;
}

.quadrant {
  display: flex;
  flex-direction: row;
  width: 45%;
}

.quadrant.one,
.quadrant.two {
  display: flex;
  flex-direction: row-reverse;
  align-items: flex-start;
  justify-content: flex-end;
  padding-top: 30px;
}

.quadrant.three,
.quadrant.four {
  display: flex;
  align-items: flex-end;
  padding-bottom: 30px;
}

.game-container {
  position: relative;
}

.dice-container {
  display: flex;
  position: absolute;
  justify-content: space-evenly;
  top: 50%;
  right: 25%;
  transform: translate(50%, -50%);
  width: 25%;
  height: 50px;
}

.dice,
.roll-button {
  height: 45px;
  width: 45px;
}

.dice-image {
  width: 100%;
  height: 100%;
}

.discard-pile {
  height: 210px;
}

.discard-pile.two {
  display: flex;
  flex-direction: column-reverse;
}
